<template>
<v-container>
  

  
<v-card-title primary-title class="justify-center">
  Cria sua conta AcesSaude
</v-card-title>
<v-container>
    
  
  <v-row class="justify-center">
  <v-form v-model="valid" ref="form" lazy-validation v-if="clinica">
  <v-col cols="12" class="ma-0 pa-0">
              <v-row class="mt-n4 pa-2" justify="center">
                  <v-card flat tile>
                                           <vUploadCloud 
            v-model="clinica.foto_perfil"
            upload-preset="hfljpegu"
            cloud-name="djwxazf5a"
          />
                  </v-card>
              </v-row>
    <v-text-field 
      label="Nome da Clinica"
      v-model="clinica.nome"
      outlined
      required
      :rules="[v => !!v || 'Informe o nome da clinica!']"

    ></v-text-field>
    </v-col>
    <v-col cols="12" class="ma-0 pa-0">
    <v-text-field
    label="CNPJ"
    v-model="clinica.cnpj"
    :counter="18"
    outlined
    required
    :rules="[v => !!v || 'Informe uma CNPJ!']"

    v-mask="'##.###.###/####-##'"
    ></v-text-field>
</v-col>
  <v-col class="ma-0 pa-0">
    <v-row>
      <v-col cols="6">
    <v-text-field
      label="telefone"
      v-model="clinica.telefone"
      outlined
      required
      :rules="[v => !!v || 'Informe um telefone!']"

      v-mask="['(##) # ####-####', '(##) ####-####']"
    ></v-text-field>
    </v-col>

    <v-col cols="6">
    <v-text-field
      label="Whatsapp"
      v-model="clinica.whatsapp"
      outlined
      v-mask="'(##) # ####-####'"

    ></v-text-field>
    </v-col>
    </v-row>
    </v-col>
      <v-col cols="12" class="ma-0 pa-0">
    <v-text-field
      label="CEP"
      v-model="clinica.cep"
      outlined
      required
      v-mask="'#####-###'"
      @keyup="preencherCep"
      
    ></v-text-field>
  </v-col>
    <v-col class="ma-0 pa-0">
    <v-row >
      <v-col cols="9" >
    <v-text-field
      label="Cidade"
      v-model="clinica.cidade"
      outlined
      required
    ></v-text-field>
    </v-col>

    <v-col cols="3">
    <v-text-field
      label="Estado"
      v-model="clinica.estado"
      outlined
      required
    ></v-text-field>
    </v-col>
    </v-row>
    </v-col>
    <v-col class="ma-0 pa-0">
    <v-row >
      <v-col cols="9" >
    <v-text-field
      label="Endereco"
      v-model="clinica.endereco"
      outlined
      required
    ></v-text-field>
    </v-col>

    <v-col cols="3">
    <v-text-field
      label="Numero"
      v-model="clinica.numero"
      outlined
      required
    ></v-text-field>
    </v-col>
    </v-row>
    </v-col>    
    <v-col class="ma-0 pa-0">
    <v-row > 
      <v-col cols="6">
    <v-text-field
      label="Bairro"
      v-model="clinica.bairro"
      outlined
      required
    ></v-text-field>
    </v-col>

    <v-col cols="6">
    <v-text-field
      label="Complemento"
      v-model="clinica.complemento"
      outlined
      required
    ></v-text-field>
    </v-col>
    </v-row>
    </v-col>
  <v-col cols="12" class="ma-0 pa-0">
    <v-text-field
      label="E-mail"
      v-model="clinica.email"
      :rules="emailRules"
      outlined
      required
    ></v-text-field>
  </v-col>
  <v-col cols="12" class="ma-0 pa-0">
    <v-text-field
      label="Senha"
      type="password"
      v-model="clinica.senha"
      :rules="[rules.required, rules.min]"

      outlined
      required
    ></v-text-field>
  </v-col>
  <v-col class="ma-0 pa-0">
    <v-textarea
      label="Descrição"
      v-model="clinica.descricao"
      textarea
      outlined
    ></v-textarea>
  </v-col>
  <v-col>
    area para fotos
  </v-col>

  <v-col>
    <v-checkbox
      v-model="terms"
      :rules="[v => !!v || 'Aceita para continuar!']"
      label="Eu aceito os termos."
      required
    ></v-checkbox>
</v-col>
<v-col>
    <v-btn
      @click="submit"
      :disabled="!valid"
      class="mr-4"
      color="primary"
      block
    >
      Criar Conta
    </v-btn>
  </v-col>
  </v-form>
  </v-row>
  
    </v-container>
    </v-container>

</template>


<script>
import { api, getCep } from "@/services/api"
import { mask } from "vue-the-mask"
import vUploadCloud from '@/components/v-cloudinary-upload'

export default {
  name: "Clinica",
  directives: { mask },
  components: { vUploadCloud },
  data: () => ({
    valid: true,
    clinica: {
        email: null,
        cidade: null,
        senha: '',
        cnpj: null,
        estado: null,
        foto_portfolio: 'asdasds',
        numero: null,
        descricao: null,
        telefone: null,
        foto_perfil: '',
        bairro: null,
        whatsapp: null,
        cep: '',
        nome: null,
        complemento: null,
        endereco: null
    },
     terms: false,
      rules: {
          required: value => !!value || 'Forneça uma senha.',
          min: v => v.length >= 8 || 'Use 8 caracteres ou mais para sua senha!',
        },
      emailRules: [
        v => !!v || 'Informe um E-mail',
        v => /.+@.+\..+/.test(v) || 'O E-mail deve ser válido',
      ]
  }),

  methods: {
    preencherCep(){
        if (this.clinica.cep.length === 9) {
          getCep(this.clinica.cep).then(response => {
            this.clinica.endereco = response.data.logradouro,
            this.clinica.bairro = response.data.bairro,
            this.clinica.estado = response.data.uf,
            this.clinica.cidade = response.data.localidade
        })
      }
    },
    
    submit() {
    if (this.$refs.form.validate()) {
        api.post("/clinica", this.clinica).then(response => {
           this.$router.push("/");
        });
    }else{
      this.valid = false
    }
    },
    clear() {
      this.$refs.form.reset();
    }
  }
};
</script>

